@import "/Users/kdh/Desktop/rookie-web/news/frontend/src/config/_variables.scss";
.main{
    .buttonWhite {
    background-color: white;
    color: black;
    border: none;
    padding: 20px 32px;
    text-align: center;
    margin : 10px;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    -webkit-transition-duration: 0.4s;
    /* Safari */
    transition-duration: 0.4s;
    cursor: pointer;
    }

    .buttonWhite:hover {
    background-color: #555555;
    color: white;
    }

    .buttonWhite:focus {
    outline: none;
    }
    .buttonBlack {
      background-color: #555555;
      color: white;
      border: none;
      padding: 20px 32px;
      text-align: center;
      margin: 10px;
      text-decoration: none;
      display: inline-block;
      font-size: 16px;
      -webkit-transition-duration: 0.1s;
      /* Safari */
      transition-duration: 0.1s;
      cursor: pointer;
    }

    .buttonBlack:hover {
      background-color: white;
      color: black;
    }

    .buttonBlack:focus {
      outline: none;
    }
}



